<!--我的订单组件-->
<template>
  <div id="order" v-if="currentSubNav">
    <div>
      <ul class="subnav" v-if="currentPageIdentify === 'ORDER_LIST'">
        <li
          v-for="(item, index) in subNavList"
          :class="currentSubNav.value === item.value ? 'active' : ''"
          @click="selectSubNav(item)"
        >
          {{ item.label }}
          <span v-if="orderAmount && getOrderNum(item.value) > 0">({{ getOrderNum(item.value) }})</span>
        </li>
      </ul>
      <ul class="subnav" v-else>
        <li
          v-for="(item, index) in orderNavList"
          :class="currentSubNav.value === item.value ? 'active' : ''"
          @click="selectSubNav(item)"
        >
          {{ item.label }}
        </li>
      </ul>
      <el-select v-model="time" placeholder="请选择" @change="getOrderInfoByTime()">
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <!-- 订单列表 -->
      <div class="content-wrapper" v-if="!isDetail && orderInfo">
        <div v-for="(order, index) in orderInfo.results" :key="index" class="order-wrapper">
          <div class="order-info">
            <span class="create-time">{{ getTime(order.createTime) }}</span>
            <span class="order-no">订单号：{{ order.orderNo }}</span>
            <span class="customer">收货人：{{ order.receiver }}</span>
            <span class="order-status">
              订单状态：<span>{{ getStatus(order.status) }}</span>
            </span>
            <template v-if="order.status === 1">
              <count-down
                class="count-down"
                @start_callback="countDownS_cb(order)"
                @end_callback="countDownE_cb(order)"
                :startTime="currentTime"
                :endTime="order.createTime + (30 * 60 * 1000)"
                :tipText="''"
                :tipTextEnd="''"
                :endText="''"
                :dayTxt="'天'"
                :hourTxt="' :'"
                :minutesTxt="' :'"
                :secondsTxt="''"
              >
              </count-down>
              <a href="javascript:;" class="cancel-order" @click="cancelOrder(order)">取消订单</a>
            </template>
            <a href="javascript:;" class="view-order" @click="viewOrder(order)">查看订单</a>
            <button
              class="to-pay"
              v-if="order.status === 1"
              @click="$router.push({ name: 'Pay', params: { orderNum: order.orderNoParent || order.orderNo, payType: 0 }})"
            >
              马上支付
            </button>
          </div>
          <div class="order" v-for="(item, index) in order.orderCommList" :key="index">
            <img
              :src="item.pictureUrl + imageMiddleWidth"
              @click="$router.push({ name: 'Detail', params: { commodityCode: item.code }})"
            >
            <span class="order-name" @click="$router.push({ name: 'Detail', params: { commodityCode: item.code }})">
              <span v-if="item.isFree">【赠品】</span>
              {{ item.name }}
            </span>
            <span class="order-count">共{{ item.count }}件</span>
            <span class="order-total">&yen; {{ item.price.toFixed(2) }}</span>
            <span class="order-status">{{ getStatus(item.status) }}</span>
            <a href="javascript:;" class="operation" @click="addCart(item)">重新加入购物车</a>
          </div>
        </div>
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-size="pageSize"
          layout="total, prev, pager, next"
          :total="orderInfo.total">
        </el-pagination>
      </div>
      <div class="order-info-wrapper" v-else-if="orderDetail">
        <div class="order-wrapper">
          <div class="order-info">
            <span class="order-status">
              订单状态：<span>{{ getStatus(orderDetail.status) }}</span>
            </span>
            <span class="order-no">订单号：{{ orderDetail.orderNo }}</span>
            <span
              class="order-verification"
              v-if="orderDetail.verification && orderDetail.status !== 0 && orderDetail.status !== 1"
            >
              提货码：
              <span>{{ orderDetail.verification }}</span>
            </span>
          </div>
          <div>
            <span class="receiver">收货人：{{ orderDetail.receiver }}</span>
            <span class="mobile">手机号：{{ orderDetail.phone }}</span>
            <span class="address">家庭住址：{{ orderDetail.addr }}</span>
          </div>
          <div class="trans-wrapper">
            <span class="trans-type">配送方式：{{ getTransStatus(orderDetail.sendType) }}</span>
            <span class="deliver-time">指定到达时间：{{ getTime(orderDetail.deliverTime) }}</span>
            <span class="site">自提点：{{ orderDetail.site }}</span>
          </div>
        </div>
        <div class="commodity-list">
          <div class="commodity-title">商品信息</div>
          <el-steps :active="getStepActive(orderDetail.status)" align-center v-if="orderDetail.status !== 0">
            <el-step title="成功提交订单"></el-step>
            <el-step title="付款成功"></el-step>
            <el-step title="配送中"></el-step>
            <el-step title="待自提"></el-step>
            <el-step title="完成"></el-step>
          </el-steps>
          <el-steps :active="getStepActive(orderDetail.status)" align-center v-else>
            <el-step title="成功提交订单"></el-step>
            <el-step title="已取消"></el-step>
          </el-steps>
          <el-table :data="orderDetail.orderLogList" stripe>
            <el-table-column prop="oprTime" label="处理时间" width="300">
              <template slot-scope="scope">
                {{ getTime(scope.row.oprTime) }}
              </template>
            </el-table-column>
            <el-table-column prop="oprContent" label="订单处理" width="500"></el-table-column>
            <el-table-column prop="oprator" label="操作人" width="200"></el-table-column>
          </el-table>
          <div clsss="commodity-wrapper">
            <div class="commodity-header">
              <span class="commodity-code">商品编号</span>
              <span class="commodity-image"></span>
              <span class="commodity-name">商品名称</span>
              <span class="commodity-price">单价</span>
              <span class="commodity-number">数量</span>
              <span class="total-amount">金额</span>
            </div>
            <div class="commodity-content" v-for="(item, index) in orderDetail.orderCommList" :key="index">
              <span class="commodity-code">{{ item.code }}</span>
              <img
                :src="item.pictureUrl + imageMiddleWidth"
                class="commodity-image"
                @click="$router.push({ name: 'Detail', params: { commodityCode: item.code } })"
              >
              <span class="commodity-name">
                <span v-if="item.isFree">【赠品】</span>
                {{ item.name }}
              </span>
              <template v-if="item.isFree === 0">
                <span class="commodity-price">
                  &yen; {{ item.actPrice.toFixed(2) }}
                </span>
                <span class="commodity-number">{{ item.count }}</span>
                <span class="total-amount">&yen; {{ (item.price * item.count).toFixed(2) }}</span>
              </template>
              <template v-if="item.isFree === 1">
              <span class="commodity-price">
                &yen; {{ item.price.toFixed(2) }}
              </span>
              <span class="commodity-number">{{ item.count }}</span>
              <span class="total-amount">&yen; {{ item.subTotal.toFixed(2) }}</span>
              </template>
            </div>
          </div>
          <div class="pay-wrapper">
            <div>
              <span class="label">共{{ countNumber }}件商品，订单总金额：</span>
              <span class="value" v-if="orderDetail.orderIntegral">{{ orderDetail.orderIntegral }}积分</span>
              <span class="value" v-else>&yen; {{ orderDetail.comAmount.toFixed(2) }}</span>
            </div>
            <div v-if="orderDetail.fullDiscount">
              <span class="label">满减折扣：</span>
              <span class="value">&yen; {{ (orderDetail.fullDiscount || 0).toFixed(2) }}</span>
            </div>
            <div v-if="orderDetail.sellDiscount">
              <span class="label">买赠折扣：</span>
              <span class="value">&yen; {{ (orderDetail.sellDiscount || 0).toFixed(2) }}</span>
            </div>
            <div v-if="orderDetail.ylcardDiscount">
              <span class="label">银犁卡折扣：</span>
              <span class="value">&yen; {{ (orderDetail.ylcardDiscount || 0).toFixed(2) }}</span>
            </div>
            <div v-if="orderDetail.voucherDiscount">
              <span class="label">优惠券折扣：</span>
              <span class="value">&yen; {{ (orderDetail.voucherDiscount || 0).toFixed(2) }}</span>
            </div>
            <div v-if="orderDetail.discount">
              <span class="label">优惠折扣：</span>
              <span class="value">&yen; {{ (orderDetail.discount || 0).toFixed(2) }}</span>
            </div>
            <div>
              <span class="label">实付金额：</span>
              <span class="value">&yen; {{ (orderDetail.orderAmount || 0).toFixed(2) }}</span>
            </div>
            <div>
            <button
              class="to-pay"
              v-if="orderDetail.status === 1"
              @click="toPay(orderDetail)"
            >
              马上支付
            </button>
            <button
              class="to-pay"
              v-else-if="orderDetail.status === 0"
              @click="addCartBatch"
            >
              重新加入购物车
            </button>
            <button
              class="to-pay"
              v-else
              @click="buyAgain"
            >
              再次购买
            </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import countDown from '@/components/countDown'

// 时间格式化
function format (number) {
  return number > 9 ? number : '0' + number
}
Date.prototype.toLocaleString = function() {
  let str = this.getFullYear() + "-"
    + format(this.getMonth() + 1) + "-"
    + format(this.getDate()) + " "
    + format(this.getHours()) + ":"
    + format(this.getMinutes())
    //  + ":" + format(this.getSeconds())
  return str
}
export default {
  components: {
    countDown
  },
  data () {
    return {
      currentSubNav: null,
      orderDetail: null,
      isDetail: false,
      orderInfo: null,
      currentNavList: [],
      currentPageIdentify: 'ORDER_LIST',
      currentPage: 1,
      pageSize: 10,
      orderAmount: null,
      time: 0,
      options: [{
        value: 0,
        label: '三个月内的订单',
      }, {
        value: 4,
        label: '半年内的订单',
      }, {
        value: 1,
        label: '今年的订单',
      }, {
        value: 2,
        label: '去年的订单',
      }, {
        value: 3,
        label: '前年的订单',
      }],
      currentTime: (new Date()).getTime(),
      subNavList: [{
        value: 'all',
        label: '全部订单'
      },{
        value: 'toPay',
        label: '待付款'
      }, {
        value: 'transporting',
        label: '配送中'
      }, {
        value: 'toBePicked',
        label: '待自提'
      }/* , {
        value: 'toBeEvaluated',
        label: '待评价'
      } */],
      orderNavList: [{
        value: 'orderDetail',
        label: '订单详情'
      }]
    }
  },
  computed: {
    countNumber () {
      let sum = 0
      this.orderDetail.orderCommList.forEach(element => sum += element.count)
      return sum
    }
  },
  watch: {
    '$route' () {
      this.init()
    }
  },
  created () {
    this.init()
  },
  methods: {
    toPay(orderInfo){
      sessionStorage.setItem('lastPayPrice',orderInfo.orderAmount);
      this.$router.push({ name: 'Pay', params: { orderNum: orderInfo.orderNo, payType: 0 }});
    },
    init () {
      if (this.$route.query.subNav === 'orderDetail') {
        this.isDetail = true
        this.currentPageIdentify = 'ORDER_DETAIL'
        this.currentNavList = this.orderNavList
        this.getOrderDetail()
      } else {
        this.isDetail = false
        this.currentPageIdentify = 'ORDER_LIST'
        this.currentNavList = this.subNavList
        this.getOrderAmount()
        this.getOrderInfo()
      }
      this.setSubNav()
    },
    // 获取订单统计
    getOrderAmount () {
      this.axios.get(this.api.getOrderAmount).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.orderAmount = res.data.result
        }
      })
    },
    // 获取订单数量
    getOrderNum (type) {
      switch (type) {
        case 'all':
          return this.orderAmount.ALL
        case 'toPay':
          return this.orderAmount.UNPAIED
        case 'transporting':
          return this.orderAmount.PACKAGED
        case 'toBePicked':
          return this.orderAmount.WAIT_DISPATCHING
        case 'toBeEvaluated':
          return this.orderAmount.UN_COMMENT
      }
    },
    getOrderInfoByTime () {
      this.currentPage = 1
      this.getOrderInfo()
    },
    // 获取订单信息
    getOrderInfo () {
      let params = {
        status: null,
        start: (this.currentPage - 1) * this.pageSize,
        limit: this.pageSize,
        time: this.time
      }
      switch (this.$route.query.subNav) {
        case 'all':
          params.status = -1
          break
        case 'toPay':
          params.status = 1
          break
        case 'transporting':
          params.status = 4
          break
        case 'toBePicked':
          params.status = 5
          break
        case 'toBeEvaluated':
          params.status = 6
          break
      }
      this.axios.get(this.api.getOrderInfo + '?status=' + params.status + '&start=' + params.start + '&limit=' + params.limit + '&hours=' + params.time).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          res.data.result.results.forEach(element => {
            element.orderCommList.sort(this.compare)
          })
          this.orderInfo = res.data.result
        }
      })
    },
    // 订单商品排序比较函数
    compare (forward, next) {
      if (forward.isFree < next.isFree) {
        return -1
      } else {
        return 1
      }
    },
    // 设置子导航
    setSubNav () {
      this.currentSubNav = this.currentNavList.find(subNav => subNav.value === this.$route.query.subNav)
    },
    // 选择子导航
    selectSubNav (item) {
      this.currentPage = 1
      this.$router.push({ name: 'User', query: { nav: this.$route.query.nav, subNav: item.value }})
    },
    // 时间转换
    getTime (time) {
      time = new Date(time)
      return time.toLocaleString()
    },
    // 倒计时回调
    countDownS_cb (x) {
      // console.log(x)
    },
    countDownE_cb (order) {
      // 订单自动取消
      this.cancelOrder(order)
    },
    // 订单状态
    getStatus (value) {
      switch (value) {
        case -1:
          return '全部'
        case 0:
          return '已取消'
        case 1:
          return '待付款'
        case 2:
          return '已付款'
        case 3:
          return '待发货'
        case 4:
          return '配送中'
        case 5:
          return '待自提'
        case 6:
          return '待评价'
        case 7:
          return '待自提'
        case 8:
          return '已完成'
      }
    },
    // 步骤条状态
    getStepActive (status) {
      switch (status) {
        case -1:
          return 0
        case 0:
          return 1
        case 1:
          return 0
        case 2:
          return 1
        case 3:
          return 1
        case 4:
          return 2
        case 5:
          return 3
        case 6:
          return 4
        case 7:
          return 3
        case 8:
          return 4
      }
    },
    // 配送方式
    getTransStatus (value) {
      switch (value) {
        case 0:
          return '自提点自提'
        case 1:
          return '送货到家'
      }
    },
    // 加入购物车
    addCart (item) {
      let param = {
        commodityCode: item.code,
        num: 1,
        secKill: false
      }
      this.axios.post(this.api.save, param).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.$message({
            message: '加入购物车成功',
            type: 'warning'
          })
          this.$store.dispatch('getCartNum')
        } else {
          this.$message({
            message: res.data.remark,
            type: 'warning'
          })
        }
      })
    },
    // 批量加入购物车
    addCartBatch () {
      let params = []
      this.orderDetail.orderCommList.forEach(element => {
        if (element.isFree === 0) {
          params.push({
            commodityCode: element.code,
            num: element.count,
            secKill: false
          })
        }
      })
      this.axios.post(this.api.batchSave, params).then(res => {
        if (res.status === 200 && res.data.code < 300) {
          this.$store.dispatch('getCartNum')
        } else {
          this.$message({
            message: res.data.remark,
            type: 'warning'
          })
        }
      })
    },
    // 再次购买
    buyAgain () {
      let list = []
      this.orderDetail.orderCommList.forEach(element => {
        if (element.isFree === 0) {
          list.push({
            commodityCode: element.code,
            num: element.count,
            secKill: false
          })
        }
      })
      sessionStorage.setItem('toPayCommodityList', JSON.stringify(list))
      this.$router.push({ name: 'prePay' })
    },
    // 翻页
    handleCurrentChange (page) {
      this.currentPage = page
      window.scrollTo(0, 0)
      this.getOrderInfo()
    },
    // 查看订单详情
    viewOrder (item) {
      this.$router.push({ name: 'User', query: { nav: this.$route.query.nav, subNav: 'orderDetail', orderNo: item.orderNo }})
    },
    // 取消订单
    cancelOrder (order) {
      this.$confirm('是否取消订单？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let orderNo = (order.isParent === 2) ? order.orderNo : order.orderNoParent
        this.axios.get(this.api.cancelOrder + '?orderNo=' + orderNo).then(res => {
          if (res.status === 200 && res.data.code < 300) {
            this.$message({
              message: '取消订单成功',
              type: 'success'
            })
            // 把当前订单状态更改为已取消订单
            order.status = 0
            // 把当前订单父订单下所有子订单状态更改为已取消订单
            this.orderInfo.results.forEach(element => {
              if (order.isParent !== 2 && element.orderNoParent === order.orderNoParent) {
                element.status = 0
              }
            })
            // 刷新订单列表
            this.getOrderInfo()
          } else {
            this.$message({
              message: res.data.remark,
              type: 'error'
            })
          }
        })
      })
    },
    // 获取订单详情
    getOrderDetail () {
      this.axios.get(this.api.getOrderDetail + '?orderNo=' + this.$route.query.orderNo).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          res.data.result.orderCommList.sort(this.compare)
          this.orderDetail = res.data.result
        } else {
          this.$message({
            message: res.data.remark,
            type: 'warning'
          })
        }
      })
    }
  }
}
</script>
<style lang="stylus">
#order
  .subnav
    display: flex
    width: 100%
    height: 36px
    line-height: 36px
    border-left: 4px solid #7BBE2B
    border-bottom: 1px solid #DDD
    li
      margin-left: 2rem
      cursor: pointer
  .el-select
    margin-top: 30px
  .el-select .el-input.is-focus .el-input__inner, .el-select .el-input__inner:focus
    border-color: #DCDFE6
  .content-wrapper
    margin-top: 20px
    .order-wrapper
      padding: 10px
      border-bottom: 1px solid #DDD
      .order-info
        padding-top: 10px
        font-size: 12px
        .create-time
          vertical-align: middle
        .order-no, .customer, .order-status
          margin-left: 20px
          vertical-align: middle
        .customer
          display: inline-block
          width: 160px
          overflow: hidden
          white-space: nowrap
          text-overflow: ellipsis
        .view-order, .cancel-order
          display: inline-block
          margin-left: 20px
          cursor: pointer
          vertical-align: middle
        .view-order
          color: #FF4E00
        .count-down
          margin-left: 20px
          display: inline-block
          color: #FF0000
          vertical-align: middle
        .to-pay
          margin-left: 70px
          padding: 0 10px
          vertical-align: middle
          background: #ffc000
          border-radius: 4px
          height: 28px
          font-size: 14px
          color: #fff
          cursor: pointer
          outline: none
          border: none
      .order
        img
          width: 160px
          height: 160px
          vertical-align: middle
          cursor: pointer
        & > span, & > a
          display: inline-block
          overflow: hidden
          text-overflow: ellipsis
          white-space: nowrap
        .order-name
          padding-right: 2rem
          width: 400px
          cursor: pointer
        .order-count
          width: 70px
        .order-total
          width: 200px
          font-weight: 700
          color: #FF4E00
        .operation:hover
          color: #FF4E00
    .el-pagination
      margin-top: 30px
  .order-info-wrapper
    margin-top: 20px
    .order-wrapper
      & > div
        padding-left: 1rem
        height: 40px
        line-height: 40px
        border: 1px solid #DDD
        border-bottom: none
        & > span
          display: inline-block
        .order-status, .receiver, .trans-type
          width: 150px
          vertical-align: top
        .receiver
          overflow: hidden
          white-space: nowrap
          text-overflow: ellipsis
          span
            color: #FF4E00
        .order-no, .mobile, .deliver-time, .order-verification
          width: 250px
        .order-verification
          span
            color: #FF4E00
      .trans-wrapper
        border-bottom: 1px solid #DDD
    .commodity-list
      margin-top: 30px
      .commodity-title
        padding-left: 2rem
        height: 36px
        line-height: 36px
        border-left: 4px solid #7BBE2B
        border-bottom: 1px solid #DDD
      .el-steps
        margin-top: 50px
        width: 100%
        .el-step
          -ms-flex-shrink: 1
          flex-grow: 1
          -ms-flex-grow: 1
      .el-table
        margin-top: 50px
        .has-gutter
          color: #333
        .el-table__row
          color: #999
          &:first-child
            color: #333
      .commodity-header
        margin-top: 30px
        height:40px
        line-height: 40px
      .commodity-content
        .commodity-image
          width: 150px
          height: 150px
          cursor: pointer
        .commodity-price, .total-amount
          color: #FF4E00
          font-weight: 700
      .commodity-header, .commodity-content
        padding: 1rem
        border: 1px solid #DDD
        border-bottom: none
        & > span
          display: inline-block
          overflow: hidden
          text-overflow: ellipsis
          white-space: nowrap
        .commodity-code
          width: 150px
        .commodity-image
          width: 150px
          vertical-align: middle
        .commodity-name
          padding: 0 30px
          width: 300px
        .commodity-price
          width: 100px
        .commodity-number
          width: 100px
        &:last-child
          border-bottom: 1px solid #DDD
      .pay-wrapper
        margin-top: 30px
        text-align: right
        span
          display: inline-block
          &.value
            width: 100px
            color: #FF4E00
            font-weight: 700
        .to-pay
          margin-top: 10px
          margin-left: 70px
          padding: 0 10px
          vertical-align: middle
          background: #ffc000
          border-radius: 4px
          height: 28px
          font-size: 14px
          color: #fff
          cursor: pointer
          outline: none
          border: none
</style>



