<!--个人中心组件-->
<template>
  <div id="user-info-wrapper" style="width: 1038px">
    <ul class="subnav">
      <li>
        <i>已经保存的收货地址({{addressList&&addressList.length||0}})</i>
        <i @click="addressEditFather({county:0});" class="c-green">新增收货地址</i>
      </li>
    </ul>
    <div class="address-list-box f12">
        <div v-for="(g,i) in addressList">
          <ul>
            <li class="li-header" :class="{'isDefault':g.isDefault,'c-green':g.isDefault}"><span>{{g.contactName}}</span><i :class="{'cursor':!g.isDefault}"  @click="selectDefaultAddress(g)">{{!g.isDefault?'设为默认':'默认'}}</i></li>
            <li>{{g.address}}<span class="block">自提点 : {{g.addressAlias}}</span></li>
            <li class="li-footer"><span>{{g.mobile}}</span><span><i class="c-green cursor mr10" @click="addressEditFather(g)">修改</i><i class="cursor" @click="delAddress(g)">删除</i></span></li>
          </ul>
        </div>
    </div>
    <addressMapSet ref="mapRef" @getAddressListRs="reloadgetAddressList()"/>


 </div>
</template>

<script>

  import addressMapSet from '@/components/addressMapSet'
export default {
  name: 'addressSet',
  components:{addressMapSet},
  data () {
    return {
      addressList:[],
      customerId:sessionStorage.getItem("userInfo")&&JSON.parse(sessionStorage.getItem("userInfo")).customerId,
    }
  },
  watch: {

  },
  mounted(){
    this.$store.dispatch("setAddressTypeStatus",false)//打开地址编辑面板
  },
  computed:{
    isShowForm(){
      return this.$store.state.isShowForm
    }
  },
  created () {
    this.getAddressList()//地址列表
  },
  methods: {
    reloadgetAddressList(dt){ //$emit重载列表
      this.getAddressList()
    },
    addressEditFather(dt){
      let ref=this.$refs.mapRef;
      ref.addressEdit(dt)
    },

    delAddress(dt){//删除地址
     if( confirm("确定要删除当前地址?")){
       this.axios.get(this.api.deleteAddress+`?customerId=${this.customerId}&id=${dt.id}`).then(res => {
         if (res.status === 200 && res.data.code === 200) {
         this.$message.success('删除成功!');
         this.getAddressList()
         this.$store.dispatch("setAddressTypeStatus",false)//关闭地址编辑面板
       }
     })
     }
    },
    getAddressList(){//获取当前用户收货地址
      this.axios.get(this.api.getAddressList+`?customerId=${this.customerId}`).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          this.addressList=res.data.result || [];
        }
      })
    },
    selectDefaultAddress(dt){//选择默认地址
      if(dt.isDefault) return false
      this.axios.get(this.api.setDefaultAddress+`?customerId=${this.customerId}&id=${dt.id}`).then(res => {
        if (res.status === 200 && res.data.code === 200){
        this.$message.success('默认地址设置成功!');
        this.getAddressList()
      }
    })
    },

  }
}
</script>

<style lang="stylus"  scoped>
  .address-list-box  div  {border: 1px solid #eee; width: 492px; display: inline-block; margin: 10px}
  .address-list-box  div li {padding: 6px 20px; border-bottom: 1px solid #eee}
  .address-list-box  div li:nth-child(2){ padding: 12px 20px; line-height: 160%}
  .li-header,.li-footer{display: flex; justify-content: space-between; }
  .li-header i,.li-footer  span{ display: inline-block}
  .isDefault{ font-size: 14px; font-weight: bold;  }
#user-info-wrapper
  width: 100%
  .subnav
    display: flex

    height: 36px
    line-height: 36px
    border-left: 4px solid #7BBE2B
    border-bottom: 1px solid #DDD



    li
      margin-left: 2rem
      cursor: pointer
      display: flex
      justify-content: space-between
      width: 1000px
      box-sizing: border-box
      padding-right:10px

      i
        display:block;



</style>



