<!-- 我的关注 -->
<template>
<div id="collection">
  <ul class="subnav">
    <li
      v-for="(item, index) in subNavList"
      class="active"
    >
      {{ item.label }}
    </li>
  </ul>
  <div></div>
  <!-- 操作栏 -->
  <div class="operation-wrapper">
    <span>批量操作</span>
    <el-checkbox v-model="isSelectAll" @change="selectAll">全选</el-checkbox>
    <span class="add-cart" @click="addCartBatch">
      <i class="iconfont icon-gouwuche"></i>
      加入购物车
    </span>
    <el-button type="primary" @click="cancelCollected">取消关注</el-button>
    <el-button type="primary" @click="clearCollected">清空关注</el-button>
  </div>
  <!-- 关注列表 -->
  <ul class="collection-list">
    <li v-for="(item, index) in collectionList" :key="index" style="position: relative;">
      <span class="icon-no-stock" v-if="item.commodityStatus===3&&item.commodityStock===0">已售罄<br/>拼命补货中</span>
      <span class="icon-no-stock" v-if="item.commodityStatus===4"><i style="padding-top: 10px; display: block">商品已下架</i></span>
      <template v-if="item.presell">
        <i class="iconfont icon-yushou"></i>
        <div class="trangle"></div>
      </template>
      <img :src="item.imageUrl" @click="$router.push({ name: 'Detail', params: { commodityCode: item.commodityCode }})">
      <div class="commodity-name" @click="$router.push({ name: 'Detail', params: { commodityCode: item.commodityCode }})">{{ item.commodityName }}</div>
      <div class="commodity-price">&yen; {{ item.commoditySalesPrice.toFixed(2) }}</div>
      <div class="commodity-operation">
        <span class="add-cart" @click="addCart(item)">
          <i class="iconfont icon-gouwuche"></i>
          加入购物车
        </span>
        <el-checkbox v-model="item.checked">选择</el-checkbox>
        <span class="collection active" @click="collect(item)">
          <i class="iconfont icon-guanzhu-tianchong"></i>
          取消关注
        </span>
      </div>
    </li>
  </ul>
  <el-pagination
    v-if="collectionInfo"
    @current-change="handleCurrentChange"
    :current-page="currentPage"
    :page-size="pageSize"
    layout="total, prev, pager, next"
    :total="collectionInfo.total">
  </el-pagination>
</div>
</template>

<script>
export default {
  name: 'Collection',
  data () {
    return {
      pageSize: 9,
      currentPage: 1,
      collectionList: [],
      selectedList: [],
      collectionInfo: null,
      isSelectAll: false,
      subNavList: [{
        value: 'collectionList',
        label: '关注列表'
      }]
    }
  },
  watch: {
    collectionList: {
      handler: function (value, oldValue) {
        // 计算选中数组
        this.selectedList = this.collectionList.filter(element => element.checked) || []
        // 计算是否全选
        this.isSelectAll = this.collectionList.every(element => element.checked === true)
      },
      deep: true
    }
  },
  created () {
    this.getCollectionList()
  },
  methods: {
    // 获取关注列表
    getCollectionList () {
      this.axios.get(this.api.getCollectionList + '?pageNum=' + this.currentPage + '&pageSize=' + this.pageSize).then(res => {
        if (res.status === 200 && res.data.code === 200) {
          if (res.data.result.results) {
            res.data.result.results.forEach(element => {
              element.checked = false
            })
          }
          this.collectionInfo = res.data.result
          this.collectionList = res.data.result.results || []
        } else {
          this.$message({
            message: res.data.remark,
            type: 'warning'
          })
        }
      })
    },
    // 全选
    selectAll (value) {
      this.collectionList.forEach(element => {
        return element.checked = value
      })
    },
    // 批量加入购物车
    addCartBatch () {
      if (this.selectedList.length === 0) {
        return this.$message({
          message: '未选中商品',
          type: 'warning'
        })
      }
      let params = []
      this.selectedList.forEach(element => {
        params.push({
          commodityCode: element.commodityCode,
          num: 1,
          secKill: false
        })
      })
      this.axios.post(this.api.batchSave, params).then(res => {
        if (res.status === 200 && res.data.code < 300) {
          this.selectedList.forEach(element => element.checked = false)
          this.$store.dispatch('getCartNum')
          this.$message({
            message: '加入购物车成功',
            type: 'success'
          })
        } else {
          this.$message({
            message: res.data.remark,
            type: 'warning'
          })
        }
      })
    },
    // 取消关注
    cancelCollected () {
      if (this.selectedList.length === 0) {
        return this.$message({
          message: '未选中商品',
          type: 'warning'
        })
      }
      this.$confirm('是否取消关注？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        let params = []
        this.selectedList.forEach(element => params.push(element.id))
        this.axios.post(this.api.removeBatchCollection, params).then(res => {
          if (res.status === 200 && res.data.code < 300) {
            this.getCollectionList()
          }
        })
      }).catch(err => {})
    },
    // 清空关注
    clearCollected () {
      if (this.collectionList.length === 0) {
        return this.$message({
          message: '没有收藏的商品',
          type: 'warning'
        })
      }
      this.$confirm('是否清空关注？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.axios.post(this.api.removeAll).then(res => {
          if (res.status === 200 && res.data.code < 300) {
            this.getCollectionList()
          }
        })
      }).catch(err => {})
    },
    // 加入购物车
    addCart (item) {
      let param = {
        commodityCode: item.commodityCode,
        num: 1,
        secKill: false
      }
      this.axios.post(this.api.save, param).then(res => {
        if (res.status === 200 && res.data.code < 300) {
          this.$store.dispatch('getCartNum')
          this.$message({
            message: '加入购物车成功',
            type: 'success'
          })
        } else {
          this.$message({
            message: res.data.remark,
            type: 'warning'
          })
        }
      })
    },
    // 关注商品
    collect (item) {
      this.$confirm('是否取消收藏？', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        // 如果已经关注了，则取消
        this.axios.get(this.api.removeCollection + '?commodityCode=' + item.commodityCode).then(res => {
          if (res.status === 200 && res.data.code < 300) {
            this.getCollectionList()
          }
        })
      }).catch(err => {})
    },
    // 翻页
    handleCurrentChange (page) {
      this.currentPage = page
      window.scrollTo(0, 0)
      this.getCollectionList()
    },
  }
}
</script>

<style lang="stylus">
  .icon-no-stock{ top:80px;left: 90px}
#collection
  .subnav
    display: flex
    width: 100%
    height: 36px
    line-height: 36px
    border-left: 4px solid #7BBE2B
    border-bottom: 1px solid #DDD
    li
      margin-left: 2rem
      cursor: pointer
  .operation-wrapper
    margin-top: 10px
    padding: 10px
    width: 918px
    border: 1px solid #DDD
    .el-checkbox
      margin-left: 16px
    .el-checkbox__label
      padding-left: 6px
      color: #999
    .add-cart
      margin-left: 16px
      cursor: pointer
      color: #999
      &:hover
        color: #FF4E00
    .el-button
      margin-left: 16px
      padding: 0 1rem
      height: 28px
      line-height: 28px
      font-size: 12px
      border-radius: 3px
  .collection-list
    margin-left: -20px
    display: flex
    flex-wrap: wrap
    width: 1000px
    li
      margin-top: 20px
      margin-left: 20px
      padding: 1rem
      width: 270px
      border: 1px solid #DDD
      text-align: center
      img
        cursor: pointer
      .commodity-name
        margin-top: 10px
        overflow: hidden
        text-overflow: ellipsis
        white-space: nowrap
        cursor: pointer
        text-align: left
      .commodity-price
        margin-top: 10px
        font-size: 16px
        font-weight: 700
        color: #FF4E00
        text-align: left
      .commodity-operation
        margin-top: 10px
        text-align: left
        & > span:hover
          cursor: pointer
        .add-cart
          color: #999
          &:hover
            color: #FF4E00
        .el-checkbox
          margin-left: 10px
          .el-checkbox__label
            padding-left: 6px
            color: #999
        .collection
          margin-left: 10px
          i
            vertical-align: -1px
  .el-pagination
    margin-top: 50px
</style>



