<!--个人中心-->
<template>
  <div id="user">
    <ul class="nav">
      <li v-for="(item, index) in navList" @click="selectNav(item)" :class="currentNav === item ? 'active' : ''">
        <span v-if="item.route" @click="$router.push({ name: item.route })">{{ item.label }}</span>
        <span v-else>{{ item.label }}</span>
      </li>
    </ul>

    <div class="container">
      <keep-alive exclude="order">
        <component :is="currentNav.value"></component>
      </keep-alive>
    </div>
  </div>
</template>

<script>
import userInfo from './userInfo.vue'
import order from '../order/myOrder.vue'
import addressSet from './addressSet.vue'
import collection from './Collection.vue'

export default {
  components: {
    userInfo,
    order,
    addressSet,
    collection
  },
  data () {
    return {
      currentNav: null,
      currentSubNav: null,
      navList: [{
        value: 'userInfo',
        label: '个人信息',
        route: null,
        subNavList: [{
          value: 'basic',
          label: '基本信息'
        }, {
          value: 'modifyPassword',
          label: '修改密码'
        }],
      }, {
        value: 'order',
        label: '我的订单',
        route: null,
        subNavList: [{
          value: 'all',
          label: '全部订单'
        },{
          value: 'toPay',
          label: '待付款'
        }, {
          value: 'transporting',
          label: '配送中'
        }, {
          value: 'toBePicked',
          label: '待自提'
        } /* , {
          value: 'toBeEvaluated',
          label: '待评价'
        } */]
      }, {
        value: 'cart',
        label: '购物车',
        route: 'Cart',
      }, {
        value: 'addressSet',
        label: '收货地址',
        route: null,
        subNavList: [{
          value: 'existing',
          label: '已保存的收货地址'
        }]
      }, {
        value: 'collection',
        label: '我的关注',
        route: null,
        subNavList: [{
          value: 'collectionList',
          label: '关注列表'
        }]
      }]
    }
  },
  watch: {
    '$route' () {
      this.setNav()
    }
  },
  created () {
    this.setNav()
  },

  methods: {
    // 设置导航
    setNav () {
      let query = this.$route.query
      this.currentNav = this.navList.find(nav => nav.value === query.nav)
    },
    selectNav (item) {
      this.$store.dispatch("setAddressTypeStatus",false)//关闭地址编辑面板
      if (!item.route) this.$router.push({ name: 'User', query: { nav: item.value, subNav: item.subNavList[0].value } })
    }
  }
}
</script>

<style lang="stylus">
#user
  display: flex
  margin: 20px auto
  width: 1200px
  .active
    color: #FF4E00
  .nav
    flex-shrink: 0
    width: 130px
    border-top: 4px solid #7BBE2B
    box-shadow: 5px -1px 20px #DDD
    -webkit-box-shadow: 5px -1px 20px #DDD
    -moz-box-shadow: 5px -1px 20px #DDD
    li
      height: 46px
      line-height: 46px
      border-bottom: 1px solid #DDD
      cursor: pointer
      &:last-child
        border: none
      span
        text-indent: 40px
        display: inline-block
        width: 100%
        height: 100%
  .container
    padding-left: 2rem
    width: 1040px
</style>



